.resumeThreeRoot {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resumeThreeP2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resumeThreeMain {
    font-family: 'Montserrat';
    font-size: 13px;
    background-color: white;
    width: 766px;
    min-height: 1096px;
    /* width: 736px;
    height: 1066px; */
    display: flex;
    flex-direction: column;
    justify-items: center;
}

.resumeThreeMain input {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resumeThreeMain textarea {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resumeThree-resumeHeader {
    border: 10px solid #0E5F76;
    padding: 5% 5%;
    display: flex;
    flex-direction: row;
}

.resumeThree-image-upload-btn {
    display: none;
}

.resumeThree-image-label {
    cursor: pointer;
}

.resumeThree-Imageholder {
    border: 1px solid #0E5F76;
    height: 130px;
    width: 130px;
    margin-right: 15px;
}

.resumeThree-Nameholder {
    margin-left: 5px;
}

.resumeThree-nameBox {
    color: #0E5F76;
    font-size: 36px;
    text-align: left;
    width: 100%;
    font-weight: 600;
}

.resumeThree-positionBox {
    font-size: 1.0em;
    letter-spacing: 4px;
    font-weight: 600;
    text-align: left;
    margin-top: 2%;
    width: 100%;
}

.resumeThree-section-2 {
    margin-top: 4%;
    border: 10px solid #0E5F76;
    display: flex;
    height: 100%;
}

.resumeThree-section-2-P2 {
    border: 10px solid #0E5F76;
    display: flex;
    height: 100%;
}

.resumeThree-left-section {
    width: 320px;
    padding: 25px;
    background-color: #0E5F76;
    color: white;
    display: flex;
    flex-direction: column;
}

.resumeThree-left-section input {
    color: white;
}

.resumeThree-left-section textarea {
    color: white;
}

.resumeThree-right-section {
    width: 426px;
    padding: 25px;
    display: flex;
    flex-direction: column;
}

.resumeThree-inputs {
    display: flex;
    flex-direction: column;
}

.resumeThree-section {
    margin-top: 25px;
}

.resumeThree-text-color {
    color: #0E5F76;
}

.resumeThree-left-section-title {
    color: white;
    font-weight: 700;
}

.resumeThree-right-section-title {
    color: #0E5F76;
    font-weight: 700;
}

.resumeThree-descriptionBox {
    color: white;
    resize: none;
    overflow: hidden;
}

.resumeThree-capBox {
    text-transform: uppercase;
}

.resumeThree-certificate-ul, .resumeThree-project-ul {
    direction: ltr;
    color: white;
}

.resumeThree-recommendation-list textarea {
    width: 100%;
    color: white;
}