.editor-root {
    background-color: black;
}

.editor-main {
    position: fixed;
    left: 0;
    padding: 4rem 0;
    color: white;
    height: 100%;
}

.editor-buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 1rem;
}

.editor-download-add-section {
    display: flex;
    margin-bottom: 10%;
}

.editor-download-btn-section {
    margin: 0 5px;
}

.editor-download-btn:focus {
    outline: none;
}

.editor-add-btn-section {
    margin: 0 5px;
}

.editor-page-add-btn:focus {
    outline: none;
}

.editor-color-icon {
    cursor: pointer;
}

.editor-font-icon {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.editor-font-icon:hover span {
    cursor: pointer;
}

.editor-font-1 {
    font-family: 'Montserrat';
    font-size: 20px;
    margin: 4%;
}

.editor-font-2 {
    font-family: cursive;
    font-size: 20px;
}

.resume-main-section {
    display: flex;
    flex-direction: column;
    align-items: center;
}