.resumeEightRoot {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resumeEightP2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resumeEightMain input, textarea {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resumeEightMain {
    font-family: 'Montserrat';
    font-size: 12px;
    background-color: white;
    width: 766px;
    height: 1096px;
    display: flex;
    flex-direction: column;
    justify-items: center;
}

.resumeEight-section-2 {
    display: flex;
    flex-direction: row;
}

.resumeEight-image-section {
    width: 100%;
    display: inline-block;
}

.resumeEight-image {
    height: 60px;
    float: left;
}

.resumeEight-nameBox {
    font-size: 2.5rem;
    font-weight: bold;
    text-align: right;
    padding-top: 15px;
}

.resumeEight-positionBox {
    font-size: 15px;
    text-align: right;
    letter-spacing: 2px;
}

.resumeEight-descriptionBox {
    text-align: right;
    resize: none;
    overflow: hidden;
}

.resumeEight-capBox {
    text-transform: uppercase;
}

.resumeEight-inputs {
    display: flex;
    flex-direction: column;
}

.resumeEight-section {
    margin-top: 25px;
}

.resumeEight-left-section {
    width: 320px;
    height: 1096px;
    border-right: 4px solid black;
    text-align: right;
    padding-top: 45px;
    padding-right: 25px;
    padding-left: 25px;
}

.resumeEightP2-left-section {
    width: 300px;
    border-right: solid black;
    text-align: right;
    padding-left: 25px;
    padding-right: 25px;
}

.resumeEightP2-left-section input {
    text-align: right;
}

.resumeEight-left-section input {
    text-align: right;
}

.resumeEight-left-section textarea {
    text-align: right;
}

.resumeEight-right-section {
    width: 382px;
    height: 1096px;
    padding-top: 140px;
    padding-left: 25px;
    padding-right: 25px;
}

.resumeEightP2-right-section {
    width: 382px;
    padding-left: 25px;
    padding-right: 25px;
}

.resumeEight-intern-ul textarea {
    font-size: 12px;
}

.resumeEight-experience-ul textarea {
    font-size: 12px;
}

.resumeEight-certificate-ul, .resumeEight-project-ul {
    direction: rtl;
}

.resumeEight-recommendation-list textarea {
    width: 100%;
    text-align: right;
}

.resumeEight-section-title {
    letter-spacing: 2px;
    font-size: 15px;
    font-weight: 600;
}