.resumeOne-orange-color {
      /* border: 15px solid #FF7900; */
      border: 30px solid #FF7900;
}

.resumeOne-orange-text {
      color: #FF7900;
}

.resumeOne-orange-section {
      color: #FF7900;
      font-weight: 700;
      font-size: 14px;
}

.resumeOneMain input, textarea {
      background-color: inherit;
      border-style: hidden;
      outline: none;
}

.resumeOneRoot {
      display: flex;
      justify-content: center;
      margin-top: 5%;
      margin-bottom: 10%;
}

.resumeOneP2Root {
      display: flex;
      justify-content: center;
      margin-bottom: 10%;
}

.resumeOneMain {
      font-family: 'Montserrat';
      font-size: 12px;
      background-color: white;
      /* width: 766px;
      height: 1096px; */
      width: 736px;
      min-height: 1086px;
      display: flex;
      flex-direction: column;
      justify-items: center;
}

.resumeOneMain input {
      border-style: hidden;
      outline: none;
}

.resumeOneMain textarea {
      border-style: hidden;
      outline: none;
}

.resumeOne-section-2 {
      display: flex;
      flex-direction: row;
      margin-top: 38px;
}

.resumeOne-nameBox {
      font-size: 2.5rem;
      font-weight: bold;
      align-self: center;
      text-align: center;
      margin-top: 40px;
      width: 730px;
      height: 60px;
}

.resumeOne-positionBox {
      font-size: 15px;
      align-self: center;
      text-align: center;
      width: 610px;
      letter-spacing: 2px;
      height: 66px;
}

.resumeOne-descriptionBox {
      text-align: right;
      resize: none;
      overflow: hidden;
}

.resumeOne-capBox {
      text-transform: uppercase;
}

.resumeOne-inputs {
      display: flex;
      flex-direction: column;
}

.resumeOne-section {
      margin-top: 25px;
}

.resumeOne-left-section {
      width: 300px;
      border-right: solid black;
      text-align: right;
      padding-top: 30px;
      padding-right: 25px;
      padding-left: 25px;
}

.resumeOneP2-left-section {
      width: 300px;
      border-right: solid black;
      text-align: right;
      padding-left: 25px;
      padding-right: 25px;
}

.resumeOneP2-left-section input {
      text-align: right;
}

.resumeOne-left-section input {
      text-align: right;
}

.resumeOne-left-section textarea {
      text-align: right;
}

.resumeOne-right-section {
      width: 382px;
      padding-left: 25px;
      padding-right: 25px;
}

.resumeOneP2-right-section {
      width: 382px;
      padding-left: 25px;
      padding-right: 25px;
}

.resumeOne-intern-ul textarea {
      font-size: 12px;
}

.resumeOne-experience-ul textarea {
      font-size: 12px;
}

.resumeOne-certificate-ul, .resumeOne-project-ul {
      direction: rtl;
}

.resumeOne-recommendation-list textarea {
      width: 100%;
      text-align: right;
}