.resume14Root {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resume14P2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resume14Main {
    font-family: 'Montserrat';
    font-size: 13px;
    border: 5px solid #547c9a;
    background-color: #f5f5ef;
    width: 766px;
    min-height: 1096px;
    display: flex;
    flex-direction: column;
    color: #263547;
}

.resume14Main input {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resume14Main textarea {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resume14-resumeHeader {
    height: 210px;
    padding-top: 10%;
    padding-left: 5%;
    padding-right: 5%;
    text-align: center;
}

.resume14-resumeHeader input {
    text-align: center;
}

.resume14-nameBox {
    font-size: 40px;
    font-weight: 600;
    width: 100%;
    color: #547c9a;
}

.resume14-positionBox {
    font-size: 1.2em;
    width: 100%;
    font-weight: 500;
    letter-spacing: 1.5px;
}

.resume14-descriptionBox {
    width: 100%;
    resize: none;
    overflow: hidden;
}

.resume14-section-2 {
    display: flex;
    height: 100%;
    margin-top: 12%;
    margin-bottom: 5%;
}

.resume14-left-section {
    width: 45%;
    border-right: 1px solid #547c9a;
    padding-left: 65px;
    padding-right: 15px;
    padding-bottom: 10px;
    display: flex;
    flex-direction: column;
}

.resume14-right-section {
    width: 55%;
    padding-right: 85px;
    padding-left: 15px;
    display: flex;
    flex-direction: column;
}

.resume14-inputs {
    display: flex;
    flex-direction: column;
}

.resume14-section {
    margin-bottom: 25px;
}

.resume14-section-title {
    font-weight: 500;
    font-size: 14px;
    color: #263547;
    margin-bottom: 8px;
}

.resume14-capBox {
    text-transform: uppercase;
}

.resume14-certificate-ul, .resume14-project-ul {
    direction: ltr;
}

.resume14-recommendation-list textarea {
    width: 100%;
}