.resumeList-main {
    align-items: center;
    text-align: center;
    margin-top: 5%;
}

.resumeList-button {
    border-radius: 50px;
    background-color: black;
    border: none;
    padding: 8px 25px;
    outline: none;
    width: 350px;
}

.resumeList-button-text {
    font-size: 18px;
    text-decoration: none;
    color: #fff;
}

.resume-list {
    display: flex;
    flex-wrap: wrap;
    margin: 5% 5%;
}

.resume-button {
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
    justify-content: center;
}

.view-resume {
    min-width: 55%;
}

.resume-viewed {
    width: 100%;
}

.modal-header {
    border-bottom: hidden !important;
}