.resume13Root {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resume13P2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resume13Main {
    font-family: 'Montserrat';
    font-size: 13px;
    background-color: white;
    width: 766px;
    min-height: 1096px;
}

.resume13Main input {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resume13Main textarea {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resume13-resumeHeader {
    display: flex;
    padding: 65px 35px;
    padding-bottom: 0;
    padding-right: 0;
    height: 260px;
}

.resume13-image {
    border-radius: 50%;
    width: 320px;
    display: flex;
    justify-content: center;
}

.resume13-image-label {
    cursor: pointer;
    height: 150px;
    width: 150px;
    border-radius: 50%;
}

.resume13-Imageholder {
    border: 1px solid black;
    height: 150px;
    width: 150px;
    border-radius: 50%;
}

.resume13-image-upload-btn {
    display: none;
    height: 150px;
    width: 150px;
    border-radius: 50%;
}

.resume13-name-position {
    display: flex;
    flex-direction: column;
    width: 800px;
    padding-left: 30px;
}

.resume13-nameBox {
    line-height: normal;
    font-size: 38px;
    font-weight: 600;
    width: 100%;
}

.resume13-positionBox {
    font-size: 1.5em;
    font-weight: 500;
    letter-spacing: 1px;
    margin-top: 2%;
    width: 100%;
    color: #c8aa8c;
}

.resume13-contact {
    margin-top: 20px;
}

.resume13-contact-inputs {
    display: flex;
    margin: 12px 5px;
}

.resume13-contact-img {
    margin-right: 10px;
    width: 20px;
    color: #c8aa8c;
    fill: currentColor;
}

.resume13-contact-inputs input {
    width: 200px;
}

.resume13-contact-inputs textarea {
    width: 200px;
}

.resume13-capBox {
    text-transform: uppercase;
}

.resume13-text-color {
    color: #c8aa8c;
}

.resume13-section-2 {
    padding: 0 15px;
    padding-right: 0;
    margin-top: 15px;
}

.resume13-section-2-P2 {
    padding: 0 35px;
    padding-right: 0;
    margin-top: 30px;
}

.resume13-section {
    display: flex;
    margin-bottom: 10px;
}

.resume13-section-title {
    font-size: 15px;
    font-weight: 600;
    text-align: center;
    width: 255px;
}

.resume13-input-section {
    width: 430px;
    margin-top: 10px;
    border-top: 3px solid #c8aa8c;
}

.resume13-inputs {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    width: 100%;
}

.resume13-experience-inputs {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    width: 90%;
    margin-left: 60px;
}

.resume13-descriptionBox {
    width: 100%;
    resize: none;
    overflow: hidden;
}

.resume13-address {
    width: 100%;
    resize: none;
    overflow: hidden;
}