.resumeFiveRoot {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resumeFiveP2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resumeFiveMain {
    font-family: 'Montserrat';
    font-size: 12px;
    background-color: white;
    width: 766px;
    min-height: 1096px;
    display: flex;
    flex-direction: column;
    justify-items: center;
    color: white;
}

.resumeFiveMain input {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resumeFiveMain textarea {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resumeFive-resumeHeader {
    padding: 8% 5%;
    padding-bottom: 0;
    display: flex;
}

.resumeFive-image {
    border-radius: 50%;
    width: 315px;
    display: flex;
    justify-content: center;
}

.resumeFive-image-label {
    cursor: pointer;
    height: 250px;
    width: 250px;
    border-radius: 50%;
    align-items: center;
}

.resumeFive-Imageholder {
    border: 1px solid #f13180;
    height: 250px;
    width: 250px;
    border-radius: 50%;
}

.resumeFive-image-upload-btn {
    display: none;
    height: 250px;
    width: 250px;
    border-radius: 50%;
}

.resumeFive-name-position {
    display: flex;
    flex-direction: column;
    width: 55%;
    padding-left: 35px;
}

.resumeFive-nameBox {
    line-height: normal;
    color: #f13180;
    font-size: 46px;
    width: 100%;
    font-weight: 600;
}

.resumeFive-positionBox {
    font-size: 1.2em;
    font-weight: 600;
    color: black;
    letter-spacing: 2px;
    margin-top: 2%;
    margin-bottom: 2%;
    width: 100%;
}

.resumeFive-descriptionBox {
    width: 100%;
    resize: none;
    overflow: hidden;
    color: white;
}

.resumeFive-section-2 {
    display: flex;
    height: 100%;
}

.resumeFive-section-2-P2 {
    display: flex;
    height: 100%;
}

.resumeFive-left-section {
    width: 45%;
    margin: 35px;
    padding: 35px;
    display: flex;
    background-color: #f13180;
    flex-direction: column;
    text-align: center;
    color: white;
}

.resumeFive-right-section {
    width: 55%;
    padding: 70px 0;
    padding-right: 15px;
    display: flex;
    flex-direction: column;
}

.resumeFive-right-section input {
    color: black;
}

.resumeFive-right-section textarea {
    color: black;
}

.resumeFive-right-section ul {
    color: black;
}

.resumeFive-inputs {
    display: flex;
    flex-direction: column;
}

.resumeFive-inputs-left textarea {
    display: flex;
    flex-direction: column;
    color: white;
    width: 100%;
}

.resumeFive-inputs-left input {
    display: flex;
    flex-direction: column;
    color: white;
    width: 100%;
}

.resumeFive-section {
    margin-top: 25px;
}

.resumeFive-section-title {
    font-weight: 600;
    color: white;
}

.resumeFive-right-section-title {
    font-weight: 600;
    color: #f13180;
}

.resumeFive-capBox {
    text-transform: uppercase;
}

.resumeFive-text-color {
    color: #f13180;
}

.resumeFive-certificate-ul, .resumeFive-project-ul {
    direction: ltr;
}

.resumeFive-recommendation-list textarea {
    width: 100%;
}