.resume11Root {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resume11P2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resume11Main {
    font-family: 'Montserrat';
    font-size: 12px;
    background-color: white;
    width: 766px;
    min-height: 1096px;
    display: flex;
    flex-direction: column;
    justify-items: center;
}

.resume11Main input {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resume11Main textarea {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resume11-resume11 {
    height: 60px;
    width: 306px;
    background-color: #e8f1f8;
}

.resume11-resumeHeader {
    padding-left: 35px;
    padding-right: 35px;
    display: flex;
    background-color: #e8f1f8;
}

.resume11-image {
    border-radius: 50%;
    width: 240px;
    display: flex;
    justify-content: center;
}

.resume11-image-label {
    cursor: pointer;
    height: 230px;
    width: 230px;
    border-radius: 50%;
    align-items: center;
}

.resume11-Imageholder {
    border: 1px solid #263547;
    height: 230px;
    width: 230px;
    border-radius: 50%;
}

.resume11-image-upload-btn {
    display: none;
    height: 230px;
    width: 230px;
    border-radius: 50%;
}

.resume11-name-position {
    display: flex;
    flex-direction: column;
    width: 55%;
    padding-left: 65px;
    align-self: center;
}

.resume11-nameBox {
    color: #263547;
    line-height: normal;
    font-size: 48px;
    width: 100%;
    font-weight: bold;
}

.resume11-positionBox {
    font-size: 1.2em;
    letter-spacing: 4px;
    margin-top: 2%;
    width: 100%;
}

.resume11-descriptionBox {
    width: 100%;
    resize: none;
    overflow: hidden;
}

.resume11-section-2 {
    display: flex;
    height: 100%;
}

.resume11-section-2-P2 {
    display: flex;
    height: 100%;
}

.resume11-left-section {
    width: 40%;
    padding: 55px;
    display: flex;
    background-color: #e8f1f8;
    flex-direction: column;
}

.resume11-right-section {
    width: 60%;
    padding: 55px 35px;
    display: flex;
    flex-direction: column;
}

.resume11-inputs {
    display: flex;
    flex-direction: column;
}

.resume11-section {
    margin-top: 25px;
}

.resume11-section-title {
    letter-spacing: 2px;
    color: #0c2e4d;
    font-weight: 900;
}

.resume11-capBox {
    text-transform: uppercase;
}

.resume11-text-color {
    color: #263547;
}

.resume11-certificate-ul, .resume11-project-ul {
    direction: ltr;
}

.resume11-recommendation-list textarea {
    width: 100%;
}