.resumeTenRoot {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resumeTenP2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resumeTenMain {
    font-family: 'Montserrat';
    font-size: 13px;
    background-color: #9ad3d5;
    width: 766px;
    min-height: 1096px;
    color: black;
}

.resumeTenMain input {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resumeTenMain textarea {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resumeTen-section-2 {
    display: flex;
    margin-top: 5%;
}

.resumeTen-left-section {
    width: 42%;
    min-height: 1010px;
    background-color: #fff5b0;
    margin-left: 30px;
    padding: 30px 30px;
    display: flex;
    flex-direction: column;
    text-align: center;
}

.resumeTen-left-section-P2 {
    width: 42%;
    min-height: 1010px;
    background-color: #fff5b0;
    margin-left: 30px;
    padding: 30px 30px;
    display: flex;
    flex-direction: column;
}

.resumeTen-left-section input {
    text-align: center;
}

.resumeTen-left-section textarea {
    text-align: center;
}

.resumeTen-right-section {
    width: 58%;
    padding: 0 30px;
    border-left: 10px solid black;
    margin-left: 30px;
    display: flex;
    flex-direction: column;
}

.resumeTen-image {
    border-radius: 50%;
    display: flex;
    justify-content: center;
}

.resumeTen-image-label {
    cursor: pointer;
    height: 180px;
    width: 180px;
    border-radius: 50%;
    align-items: center;
}

.resumeTen-Imageholder {
    border: 1px solid black;
    height: 180px;
    width: 180px;
    border-radius: 50%;
}

.resumeTen-image-upload-btn {
    display: none;
    height: 180px;
    width: 180px;
    border-radius: 50%;
}

.resumeTen-capBox {
    text-transform: uppercase;
}

.resumeTen-nameBox {
    font-size: 40px;
    font-weight: 600;
    width: 100%;
    text-align: center;
    align-self: center;
    margin-top: 2%;
}

.resumeTen-positionBox {
    font-size: 1.0em;
    font-weight: 600;
    letter-spacing: 2.8px;
    margin-top: 3%;
    width: 100%;
    text-align: center;
    align-self: center;
    resize: none;
    overflow: hidden;
}

.resumeTen-descriptionBox {
    width: 100%;
    resize: none;
    overflow: hidden;
}

.resumeTen-inputs {
    display: flex;
    flex-direction: column;
}

.resumeTen-section {
    margin-top: 25px;
}

.resumeTen-section-title {
    font-weight: 600;
    font-size: 20px;
    color: black;
}

.resumeTen-text-color {
    color: black;
}

.resumeTen-certificate-ul, .resumeTen-project-ul {
    direction: ltr;
}

.resumeTen-recommendation-list textarea {
    width: 100%;
}

.resumeTen-flex {
    display: flex;
    height: 100%;
    align-self: center;
}

