@media (max-width: 500px) {
    .footer-parent {
        background: rgb(36, 36, 36);
    }
}

.footer-parent span {
    cursor: pointer;
}

.footer-parent {
    font-size: 13px;
    background-image: url('../../../assets/images/footer.svg');
    background-size: 100%;
    overflow: hidden;
    background-repeat: no-repeat;
}

.global-footer {
    height: 100%;
    background-color: rgba(255, 255, 255, 0);
    color: #ffff;
    display: flex;
    justify-content: space-between;
}

.footer-container {
    display: flex;
    width: 100%;
    padding-top: 5%;
    flex-wrap: nowrap;
    align-content: center;
    align-items: flex-start;
    justify-content: space-around;
}

.footer-credits {
    bottom: 0;
    width: 100%;
    height: 1%;
    background-color: white;
    font-size: 13px;
    padding-left: 5%;
}

.footer-logo {
    height: 140px;
}

.sub-headings {
    color: #bbbb;
}

.footer-links>a {
    text-decoration: none;
}