.resume17Root {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resume17P2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resume17Main {
    font-family: 'Montserrat';
    font-size: 12px;
    background-color: white;
    width: 766px;
    height: 1096px;
    display: flex;
    flex-direction: column;
    justify-items: center;
}

.resume17Main input {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resume17Main textarea {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resume17-resumeHeader {
    padding: 8% 4%;
    padding-left: 60px;
    padding-bottom: 0;
    display: flex;
    height: 230px;
    background-color: #2251b2;
}

.resume17-image {
    margin-right: 35px;
}

.resume17-image-label {
    background-color: white;
    cursor: pointer;
    height: 270px;
    width: 240px;
    align-items: center;
}

.resume17-Imageholder {
    background-color: white;
    border: 1px solid #5f4b8b;
    height: 270px;
    width: 240px;
}

.resume17-image-upload-btn {
    background-color: white;
    display: none;
    height: 270px;
    width: 240px;
}

.resume17-name-position {
    display: flex;
    flex-direction: column;
    width: 55%;
}

.resume17-nameBox {
    line-height: normal;
    color: white;
    font-size: 46px;
    width: 100%;
    font-weight: 600;
    letter-spacing: 1.5px;
}

.resume17-positionBox {
    font-size: 1.2em;
    color: white;
    letter-spacing: 2px;
    margin-bottom: 2%;
    font-weight: 600;
    width: 100%;
}

.resume17-descriptionBox {
    width: 100%;
    resize: none;
    overflow: hidden;
}

.resume17-section-2 {
    display: flex;
    height: 100%;
}

.resume17-section-2-P2 {
    display: flex;
    height: 100%;
}

.resume17-left-section {
    width: 300px;
    padding: 144px 60px;
    padding-right: 0;
    display: flex;
    flex-direction: column;
}

.resume17-left-section-P2 {
    width: 300px;
    padding: 30px 60px;
    padding-right: 0;
    display: flex;
    flex-direction: column;
}

.resume17-right-section {
    width: 55%;
    padding: 35px;
    padding-top: 15px;
    display: flex;
    flex-direction: column;
}

.resume17-inputs {
    display: flex;
    flex-direction: column;
}

.resume17-section {
    margin-top: 25px;
}

.resume17-section-title {
    border-top: 2px solid #e69a8d;
    padding-top: 8px;
    font-weight: 600;
}

.resume17-contact-section-title {
    padding-top: 8px;
    font-weight: 600;
}

.resume17-capBox {
    text-transform: uppercase;
}

.resume17-text-color {
    color: #5f4b8b;
}

.resume17-certificate-ul, .resume17-project-ul {
    direction: ltr;
}

.resume17-recommendation-list textarea {
    width: 100%;
}