.resume15Root {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resume15P2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resume15Main {
    font-family: 'Montserrat';
    font-size: 13px;
    background-color: #f5f5ef;
    width: 766px;
    min-height: 1096px;
    display: flex;
    flex-direction: column;
    padding: 55px 45px;
}

.resume15Main input {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resume15Main textarea {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resume15-resumeHeader {
    display: flex;
    text-align: left;
}

.resume15-image-upload-btn {
    display: none;
}

.resume15-image-label {
    cursor: pointer;
}

.resume15-Imageholder {
    border: 1px solid #318ba2;
    height: 170px;
    width: 160px;
    margin-right: 15px;
}

.resume15-name-position {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.resume15-nameBox {
    font-size: 40px;
    font-weight: 600;
    width: 100%;
    color: #318ba2;
    height: 60px;
}

.resume15-positionBox {
    font-size: 1em;
    width: 100%;
}

.resume15-contact-section {
    margin-top: 15px;
    border-top: 1px solid #318ba2;
    border-bottom: 1px solid #318ba2;
    display: flex;
    font-size: 10px;
    font-weight: bold;
    padding: 10px 0;
}

.resume15-contact-section input {
    font-weight: 500;
}

.resume15-contact-section textarea {
    font-weight: 500;
}

.resume15-contact {
    display: flex;
    flex-direction: column;
}

.resume15-contact-address {
    display: flex;
    flex-direction: column;
    width: 150%;
    border-right: 1px solid #318ba2;
}

.resume15-contact-title {
    font-weight: 600;
    font-size: 10px;
    color: #263547;
}

.resume15-address {
    resize: none;
    overflow: hidden;
}

.resume15-contact-phone {
    width: 120px;
    padding-left: 10px;
    border-right: 1px solid #318ba2;
}

.resume15-contact-website {
    padding-left: 10px;
}

.resume15-profile {
    margin-top: 30px;
}

.resume15-descriptionBox {
    width: 100%;
    resize: none;
    overflow: hidden;
    margin-bottom: 15px;
}

.resume15-section-2 {
    display: flex;
    border-top: 1px solid #318ba2;
    padding: 25px 0;
}

.resume15-section-3 {
    display: flex;
    border-top: 1px solid #318ba2;
    padding: 25px 0;
}

.resume15-section-2-P2 {
    display: flex;
}

.resume15-left-section {
    width: 50%;
    border-right: 1px solid #318ba2;
    display: flex;
    flex-direction: column;
    padding-right: 8px;
}

.resume15-right-section {
    width: 50%;
    margin-left: 25px;
    display: flex;
    flex-direction: column;
}

.resume15-inputs {
    display: flex;
    flex-direction: column;
}

.resume15-section {
    margin-top: 20px;
}

.resume15-section-title {
    font-weight: 500;
    font-size: 18px;
    color: #263547;
    margin-bottom: 8px;
    letter-spacing: 0px;
}

.resume15-capBox {
    text-transform: uppercase;
}

.resume15-certificate-ul, .resume15-project-ul {
    direction: ltr;
}

.resume15-recommendation-list textarea {
    width: 100%;
}