.resumeTwoRoot {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resumeTwoP2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resumeTwoMain {
    font-family: 'Montserrat';
    font-size: 13px;
    background-color: white;
    width: 766px;
    height: 1096px;
    display: flex;
    flex-direction: column;
    justify-items: center;
}

.resumeTwoMain input {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resumeTwoMain textarea {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resumeTwo-resumeHeader {
    margin-top: 40px;
    padding: 8px 48px;
    display: flex;
    background-color: #eff9ff;
    border-bottom: 15px solid #d3e4ef;
    border-radius: 0px 150px 150px 0px;
    margin-right: 15px;
    padding-right: 12px;
}

.resumeTwo-image {
    border-radius: 50%;
    width: 200px;
    display: flex;
    justify-content: flex-end;
}

.resumeTwo-image-label {
    cursor: pointer;
    height: 180px;
    width: 180px;
    border-radius: 50%;
}

.resumeTwo-Imageholder {
    border: 1px solid #5666ad;
    height: 180px;
    width: 180px;
    border-radius: 50%;
}

.resumeTwo-image-upload-btn {
    display: none;
    height: 180px;
    width: 180px;
    border-radius: 50%;
}

.resumeTwo-name-position {
    display: flex;
    flex-direction: column;
    width: 566px;
}

.resumeTwo-nameBox {
    line-height: normal;
    color: #5666ad;
    font-size: 46px;
    width: 100%;
    font-weight: 600;
}

.resumeTwo-positionBox {
    font-size: 1.2em;
    color: #5666ad;
    letter-spacing: 2px;
    margin-top: 2%;
    margin-bottom: 2%;
    width: 100%;
}

.resumeTwo-descriptionBox {
    width: 500px;
    resize: none;
    overflow: hidden;
}

.resumeTwo-section-2 {
    display: flex;
    height: 100%;
}

.resumeTwo-section-2-P2 {
    display: flex;
    height: 100%;
}

.resumeTwo-left-section {
    width: 55%;
    padding: 38px 48px;
    display: flex;
    flex-direction: column;
}

.resumeTwo-right-section {
    width: 45%;
    background-color: #f6fafd;
    margin: 25px 0;
    margin-right: 25px;
    padding: 15px 25px;
    border-radius: 2%;
    display: flex;
    flex-direction: column;
}

.resumeTwo-inputs {
    display: flex;
    flex-direction: column;
}

.resumeTwo-section {
    margin-top: 25px;
}

.resumeTwo-section-title {
    font-size: 18px;
    font-weight: 600;
}

.resumeTwo-capBox {
    text-transform: uppercase;
}

.resumeTwo-text-color {
    color: #5666ad;
}

.resumeTwo-certificate-ul, .resumeTwo-project-ul {
    direction: ltr;
}

.resumeTwo-recommendation-list textarea {
    width: 100%;
}