.resume12Root {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resume12P2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resume12Main {
    font-family: 'Montserrat';
    font-size: 13px;
    background-color: white;
    width: 766px;
    min-height: 1096px;
    /* width: 736px;
    height: 1066px; */
    display: flex;
    flex-direction: column;
    justify-items: center;
}

.resume12Main input {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resume12Main textarea {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resume12-resumeHeader {
    padding: 3% 5%;
    display: flex;
    flex-direction: row;
    background-color: #6cc3e5;
}

.resume12-image-upload-btn {
    border-radius: 50%;
    display: none;
}

.resume12-image-label {
    border-radius: 50%;
    cursor: pointer;
}

.resume12-Imageholder {
    border-radius: 50%;
    border: 1px solid #0E5F76;
    height: 150px;
    width: 150px;
    margin-right: 15px;
}

.resume12-Nameholder {
    margin-left: 15px;
    align-self: center;
}

.resume12-nameBox {
    color: white;
    font-size: 40px;
    text-align: left;
    width: 100%;
    font-weight: 500;
}

.resume12-positionBox {
    font-size: 1.2em;
    letter-spacing: 1px;
    font-weight: 600;
    text-align: left;
    width: 100%;
    margin-top: 1%;
}

.resume12-section-2 {
    display: flex;
    height: 100%;
}

.resume12-section-2-P2 {
    display: flex;
    height: 100%;
}

.resume12-left-section {
    width: 320px;
    padding: 40px;
    display: flex;
    flex-direction: column;
}

.resume12-right-section {
    width: 426px;
    padding: 40px;
    display: flex;
    flex-direction: column;
}

.resume12-inputs {
    display: flex;
    flex-direction: column;
}

.resume12-inputs-border {
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid #d2d1d4;
    padding-bottom: 15px;
}

.resume12-section {
    margin-top: 18px;
}

.resume12-text-color {
    color: #41b2dc;
}

.resume12-section-title {
    font-weight: 500;
    font-size: 16px;
    color: #f48237;
}

.resume12-descriptionBox {
    resize: none;
    overflow: hidden;
}

.resume12-capBox {
    text-transform: uppercase;
}

.resume12-certificate-ul, .resume12-project-ul {
    direction: ltr;
}

.resume12-recommendation-list textarea {
    width: 100%;
}