.resumeFourRoot {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resumeFourP2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resumeFourMain {
    font-family: 'Montserrat';
    font-size: 13px;
    background-color: white;
    width: 766px;
    min-height: 1096px;
    display: flex;
    flex-direction: column;
    justify-items: center;
    border-top: 20px solid #c2331a;
    border-bottom: 20px solid #c2331a;
}

.resumeFourMain input {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resumeFourMain textarea {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resumeFour-resumeHeader {
    display: flex;
    padding: 35px;
    height: 260px;
}

.resumeFour-image {
    border-radius: 50%;
    width: 210px;
    display: flex;
    justify-content: center;
}

.resumeFour-image-label {
    cursor: pointer;
    height: 150px;
    width: 150px;
    border-radius: 50%;
}

.resumeFour-Imageholder {
    border: 1px solid #c2331a;
    height: 150px;
    width: 150px;
    border-radius: 50%;
}

.resumeFour-image-upload-btn {
    display: none;
    height: 150px;
    width: 150px;
    border-radius: 50%;
}

.resumeFour-name-position {
    display: flex;
    flex-direction: column;
    width: 240px;
}

.resumeFour-nameBox {
    line-height: normal;
    font-size: 38px;
    font-weight: 500;
    width: 100%;
}

.resumeFour-positionBox {
    font-size: 1.2em;
    font-weight: bold;
    letter-spacing: 1px;
    margin-top: 10%;
    width: 100%;
}

.resumeFour-contact-inputs {
    width: 240px;
    margin-top: 15px;
    display: flex;
    flex-direction: column;
}

.resumeFour-contact-inputs input {
    margin-bottom: 6px;
    text-align: right;
}

.resumeFour-contact-inputs textarea {
    margin-bottom: 6px;
    text-align: right;
}

.resumeFour-capBox {
    text-transform: uppercase;
}

.resumeFour-text-color {
    color: #c2331a;
}

.resumeFour-section-2 {
    padding: 0 35px;
    padding-right: 0;
}

.resumeFour-section-2-P2 {
    padding: 0 35px;
    padding-right: 0;
    margin-top: 30px;
}

.resumeFour-section {
    display: flex;
    margin-bottom: 15px;
}

.resumeFour-section-title {
    font-size: 15px;
    font-weight: 600;
    text-align: right;
    width: 190px;
}

.resumeFour-input-section {
    margin-left: 30px;
    margin-top: 10px;
    width: 540px;
    border-top: 5px solid #c2331a;
}

.resumeFour-inputs {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    width: 90%;
}

.resumeFour-experience-inputs {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    width: 90%;
    margin-left: 60px;
}

.resumeFour-descriptionBox {
    resize: none;
    overflow: hidden;
}

.resumeFour-certificate-ul, .resumeFour-project-ul {
    direction: ltr;
}

.resumeFour-recommendation-list textarea {
    width: 100%;
}