.resumeNineRoot {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resumeNineP2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resumeNineMain {
    font-family: 'Montserrat';
    font-size: 13px;
    border-bottom: 30px solid #ff8882;
    background-color: #f8f8ef;
    width: 766px;
    min-height: 1096px;
}

.resumeNineMain input {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resumeNineMain textarea {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resumeNine-resumeHeader {
    height: 270px;
    text-align: center;
    display: flex;
}

.resumeNine-name-position {
    align-self: flex-end;
    text-align: center;
    width: 100%;
}

.resumeNine-capBox {
    text-transform: uppercase;
}

.resumeNine-nameBox {
    font-size: 50px;
    font-weight: 500;
    letter-spacing: 0.5px;
    width: 100%;
    height: 70px;
    color: #ff8882;
    text-align: center;
    align-self: center;
}

.resumeNine-positionBox {
    font-size: 1.2em;
    font-weight: 600;
    letter-spacing: 2.8px;
    margin-top: 2%;
    width: 100%;
    text-align: center;
    align-self: center;
}

.resumeNine-descriptionBox {
    width: 100%;
    resize: none;
    overflow: hidden;
}

.resumeNine-section-2 {
    display: flex;
    margin-top: 5%;
}

.resumeNine-left-section {
    width: 42%;
    padding: 0 45px;
    display: flex;
    flex-direction: column;
}

.resumeNine-right-section {
    width: 58%;
    padding: 0 45px;
    display: flex;
    flex-direction: column;
}

.resumeNine-inputs {
    display: flex;
    flex-direction: column;
}

.resumeNine-section {
    margin-top: 25px;
}

.resumeNine-section-title {
    font-weight: 500;
    font-size: 15px;
    color: #ff8882;
}

.resumeNine-text-color {
    color: #ff8882;
}

.resumeNine-certificate-ul, .resumeNine-project-ul {
    direction: ltr;
}

.resumeNine-recommendation-list textarea {
    width: 100%;
}