.resume-button {
    position: absolute;
    width: inherit;
}

.resume-button:hover{
    background-color:rgba(0,0,0,0.6);
    position: absolute;
    transition: linear .2s;
}

.resume-button:focus{
    background-color:rgba(0,0,0,0);
    border: none;
    transition: linear .2s;
}


.resume-button:hover .resume-top-icon{
    opacity: 1;    
}
.resume-top-icon {
    margin: 90px 35px;
    opacity: 0;
}