.resumeSixRoot {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resumeSixP2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resumeSixMain {
    font-family: 'Montserrat';
    font-size: 13px;
    background-color: #e5fcff;
    color: #212121;
    width: 766px;
    min-height: 1096px;
    /* width: 736px;
    height: 1066px; */
    display: flex;
    flex-direction: column;
    justify-items: center;
}

.resumeSixMain input {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resumeSixMain textarea {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resumeSix-resumeHeader {
    text-align: center;
    height: 210px;
    padding: 8% 5%;
    border-bottom: 3px dashed #212121;
}

.resumeSix-nameBox {
    font-size: 40px;
    text-align: left;
    width: 100%;
    font-weight: 600;
}

.resumeSix-positionBox {
    font-size: 1.2em;
    letter-spacing: 2px;
    text-align: center;
    margin-top: 2%;
    width: 100%;
}

.resumeSix-descriptionBox {
    font-size: 1em;
    width: 100%;
    resize: none;
    overflow: hidden;
}

.resumeSix-section-2 {
    display: flex;
    height: 100%;
}

.resumeSix-left-section {
    width: 50%;
    border-right: 3px dashed #212121;
    padding: 35px;
    display: flex;
    flex-direction: column;
}

.resumeSix-right-section {
    width: 50%;
    padding: 35px;
    display: flex;
    flex-direction: column;
}

.resumeSix-inputs {
    display: flex;
    flex-direction: column;
}

.resumeSix-section {
    margin-top: 25px;
}

.resumeSix-section-title {
    font-weight: 700;
    font-size: 18px;
}

.resumeSix-capBox {
    text-transform: uppercase;
}

.resumeSix-certificate-ul, .resumeSix-project-ul {
    direction: ltr;
}

.resumeSix-recommendation-list textarea {
    width: 100%;
}