.signup-main {
    background-color: white;
    border-radius: 0.5rem;
    padding: 3rem;
    display: flex;
}

.signup-image-section {
    width: 45%;
    display: flex;
}

.signup-image {
    width: 100%;
    height: 450px;
    flex-shrink: 0;
}

.signup-form {
    width: 55%;
}

.signup-main .btn-primary {
    background-color: white;
    border-color: #b7c2ce;
    color: black;
}

.signup-main .btn-primary:hover {
    color: black;
    border-color: #d3d6da;
    background-color: #d3d6da;
}

.google-logo {
    height: 30px;
    padding-left: 10px;
}

.signup-create-section {
    display: flex;
    justify-content: center;
    font-weight: 400;
    font-size: x-large;
}

.signup-or-section {
    text-align: center;
    display: flex;
    margin: 1rem;
}

.signup-or {
    position: relative;
    top: 12px;
    margin: 0 2px;
}

.strong-line-left, .strong-line-right {
    border-bottom: 1px solid #dfe0e6;
    width: 50%;
}

.signup-form-inputs {
    display: flex;
    flex-direction: column;
}

.signup-margin {
    margin-top: 3%;
    width: 100%;
}

.name-inputs {
    display: flex;
}

.first-name-input {
    margin-right: 10px;
}

.radio-inputs {
    margin: 3% 0;
}

.signup-submit-btn {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-top: 8%;
}

.signup-submit {
    border-radius: 25px;
    padding: 10px 25px;
    background: #FEDE00;
    border: 1px solid transparent;
    width: 100%;
}

.signup-error {
    color: red;
}