.resumeSevenRoot {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    margin-bottom: 10%;
}

.resumeSevenP2Root {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.resumeSevenMain {
    font-family: 'Montserrat';
    font-size: 13px;
    border: 15px solid #303f3c;
    background-color: #f5f5ef;
    width: 766px;
    min-height: 1096px;
    display: flex;
    flex-direction: column;
    justify-items: center;
    color: #303f3c;
}

.resumeSevenMain input {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resumeSevenMain textarea {
    background-color: inherit;
    border-style: hidden;
    outline: none;
}

.resumeSeven-resumeHeader {
    height: 240px;
    padding: 8% 5%;
}

.resumeSeven-nameBox {
    font-size: 50px;
    font-weight: 600;
}

.resumeSeven-positionBox {
    font-size: 1.2em;
    letter-spacing: 2px;
    margin-top: 1%;
    width: 100%;
}

.resumeSeven-descriptionBox {
    width: 100%;
    resize: none;
    overflow: hidden;
}

.resumeSeven-section-2 {
    display: flex;
    height: 100%;
    margin-top: 5%;
}

.resumeSeven-left-section {
    width: 45%;
    border-right: 2px solid #303f3c;
    margin-bottom: 4%;
    padding: 0 45px;
    display: flex;
    flex-direction: column;
}

.resumeSeven-right-section {
    width: 55%;
    padding: 0 45px;
    display: flex;
    flex-direction: column;
}

.resumeSeven-inputs {
    display: flex;
    flex-direction: column;
}

.resumeSeven-section {
    margin-top: 25px;
}

.resumeSeven-section-title {
    font-weight: 600;
    font-size: 17px;
}

.resumeSeven-right-section-title {
    font-weight: 500;
    font-size: 16px;
    background-color: #303f3c;
    color: #f5f5ef;
    padding: 2px 15px;
}

.resumeSeven-capBox {
    text-transform: uppercase;
}

.resumeSeven-certificate-ul, .resumeSeven-project-ul {
    direction: ltr;
}

.resumeSeven-recommendation-list textarea {
    width: 100%;
}